import React from "react";
import axios from 'axios'
import { Button, Input, Image, GeistProvider, CssBaseline } from '@geist-ui/react'


export default class Base64Base64 extends React.Component {
    state = {
        model: this.props.data.model,
        img_url: this.props.data.sample_input,
        output: null,
        loading: false,
        picture:null,
        ImageData:this.props.data.sample_input,
        token:null,
        proxyUrl:null,
        imageURL:null,
    };
     inputRef = React.createRef()

    toggleInputChange = (e) => {
        this.setState({ img_url: e.target.value, output: null});
    }

    toggleButtonState = () => {
        let url = this.state.imageURL
        this.setState({ loading: true, output: null});
        axios.post("/image", {
            body:{data:url,token:this.state.token,endpoint:this.state.proxyUrl},
            headers: {
                "Content-Type": "application/json",
              },
        })
            .then((response) => {
                return response.data.image;
            })
            .then((data) => {
                            this.setState({ output: data, loading: false});
                        })
                }
    handleFileUpload = () => {
        this.inputRef.current.click()
    }
     onChangePicture = (e) => {
        this.setState({img_url:e.target.value})
        // if (e.target.files[0]) {
        //   this.setState({Picture:e.target.files[0]});
        //   const reader = new FileReader();
        //   reader.addEventListener("load", () => {
        //     this.setState({img_url:reader.result});
        //   });
        //   reader.readAsDataURL(e.target.files[0]);
        // }
      };

    toggleInputChange = (e) => {
        this.setState({ token: e.target.value});
    }
    toggleInputEndpoint = (e) => {
        this.setState({ proxyUrl: e.target.value});
    }
    toggleInputImage = (e) =>{
        this.setState({imageURL: e.target.value})
    }


    render() {
        return (
            <GeistProvider>
                <CssBaseline />

                {/* INPUT: Image */}
                {
                    (this.state.output === null)
                        ? (this.state.img_url === "")
                        ? null
                        : <div className="w-full background-dots flex justify-center items-center" style={{height:"440px"}}>
                            <Image height="440" src={this.state.img_url} onError={(e)=>{
                                e.target.src="https://upload.wikimedia.org/wikipedia/commons/thumb/d/d1/Image_not_available.png/640px-Image_not_available.png"
                            }}/>
                        </div>
                        : <div className="w-full background-dots flex justify-center items-center" >
                            <Image height="440"  src={`data:image/png;base64,${this.state.output}`}/>
                        </div>
                }

                {/* RUN Model */}
                <div className="my-5">
                    <input type="file" ref = {this.inputRef} onChange={this.onChangePicture} style={{display:"none"}} />
                    <Input placeholder="Api Endpoint" width="100%" className="mb-2" onChange={this.toggleInputEndpoint} />
                    <Input  placeholder="Api Token" width="100%" onChange={this.toggleInputChange}  value={this.state.token} className="mb-2"/>
                    <Input placeholder="Image URL" width="100%" className="mb-2" onChange={this.onChangePicture} />
                    <Button onClick = {this.handleFileUpload} >Upload</Button>
                    <div className="flex justify-center">
                        <Button className="m-2" type="success" onClick={this.toggleButtonState} loading={this.state.loading}>Run model</Button>
                        {/*<Button className="my-2" iconRight={<RefreshCcw />} auto />*/}
                    </div>
                </div>

            </GeistProvider>
        );
    }
}